@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.page__body {
  min-width: 260px;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  color: #444444;
}

@media (min-width: 768px) {
  .main-header {
    min-height: 89px;
    position: relative;
  }
  .main-header::after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    bottom: -1px;
    left: 0;
  }
}

.main-header__wrapper {
  width: 100%;
  position: relative;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    position: relative;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 668px;
  }
}
@media (min-width: 1150px) {
  .main-header__wrapper {
    width: 1050px;
  }
}

.main-header__logo {
  z-index: 1;
  min-height: 79px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 0 #e5e5e5, inset 0 1px 0 #e5e5e5;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-header__logo {
    height: 89px;
    box-shadow: none;
  }
}
@media (min-width: 1150px) {
  .main-header__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}

.main-header__logo-image {
  flex-shrink: 0;
  margin: 0;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (min-width: 768px) {
  .main-nav {
    min-height: 89px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    width: 100%;
  }
}

.main-nav__link {
  display: flex;
  align-items: center;
  min-height: 79px;
  text-decoration: none;
  box-shadow: 0 1px 0 #e5e5e5, inset 0 1px 0 #e5e5e5;
}
.main-nav__link:hover .main-nav__link-text--search::before, .main-nav__link:hover .main-nav__link-text--cart::before {
  background-color: #63d1bb;
  opacity: 1;
}
.main-nav__link:hover .main-nav__link-text {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link:active .main-nav__link-text--search::before, .main-nav__link:active .main-nav__link-text--cart::before {
  background-color: #63d1bb;
  opacity: 0.3;
}
.main-nav__link:active .main-nav__link-text {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .main-nav__link {
    box-shadow: none;
    min-height: auto;
  }
}
@media (min-width: 1150px) {
  .main-nav__link {
    min-height: 89px;
  }
}

.main-nav__link-text {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  width: 260px;
  margin: 0 auto;
  text-align: left;
  padding-left: 46px;
}
.main-nav__link-text--search {
  position: relative;
}
.main-nav__link-text--search::before {
  content: "";
  width: 18px;
  height: 18px;
  mask-image: url("../img/stack.svg#search");
  background-color: #000000;
  opacity: 0.2;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .main-nav__link-text--search::before {
    left: -48px;
  }
}
@media (min-width: 768px) {
  .main-nav__link-text--search {
    font-size: 0;
  }
}
.main-nav__link-text--cart {
  position: relative;
}
.main-nav__link-text--cart::before {
  content: "";
  width: 21px;
  height: 18px;
  mask-image: url("../img/stack.svg#cart");
  background-color: #000000;
  opacity: 0.2;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .main-nav__link-text--cart::before {
    left: -51px;
  }
}
@media (min-width: 768px) {
  .main-nav__link-text {
    width: auto;
    padding: 0;
  }
}

.main-nav__description {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__description {
    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    display: block;
    position: absolute;
    bottom: -44px;
    right: 0;
    margin: 0;
    z-index: 1;
  }
}

.main-nav__toggle {
  height: 79px;
  width: auto;
  background-color: transparent;
  border: none;
  padding: 0;
  position: absolute;
  top: 0;
  right: 30px;
}

.main-nav__toggle::before {
  content: "";
  display: inline-block;
  width: 21px;
  height: 20px;
  background-image: url("../img/stack.svg#menu-close");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-width: 768px) {
  .main-nav__toggle::before {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  width: 20px;
  height: 14px;
  background-image: url("../img/stack.svg#menu");
}

@media (max-width: 767px) {
  .main-nav--closed .nav-list__item {
    display: none;
  }

  .main-nav--closed .user-list__item {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
  }
}

.main-nav__submenu {
  max-width: 780px;
}

